import { newE2EPage } from '@stencil/core/testing';

describe('presto-action-bar', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-action-bar></presto-action-bar>');

    const element = await page.find('presto-action-bar');
    expect(element).toHaveClass('hydrated');
  });
});
